import React from 'react';
import { ActionIcon } from '@mantine/core';
import CalendarIcon from './icons/CalendarIcon';

import { useSelector, useDispatch } from 'react-redux'
import { toggleCalendar } from '../reducers/calendarSlice'

const buttonStyle = {
  width: "60px",
  height: "60px",
  padding: "10px",
  margin: "0px",
  '--ai-bg': 'rgb(0, 0, 0, 0.2)',
  '--ai-hover': 'rgb(255, 255, 255, 0.3)',
  pointerEvents: "auto",
}

function ToggleCalendar() {
  const displayCalendar = useSelector(state => state.calendar.calendarVisible)
  const dispatch = useDispatch()

  return (
    <>
      <ActionIcon
        variant="filled"
        style={buttonStyle}
        className={displayCalendar ? 'buttonActive' : ''}
        onClick={() => dispatch(toggleCalendar())}
        styles={{
          root: {
            padding: '0',
            borderRadius: '100px',
          }
        }}
      >
        <CalendarIcon />
      </ActionIcon>
    </>
  );
}

export default ToggleCalendar;