import React from 'react';
import { ActionIcon } from '@mantine/core';
import GraphIcon from './icons/GraphIcon';

import { useSelector, useDispatch } from 'react-redux'
import { toggleGraphs } from '../reducers/graphSlice'

const buttonStyle = {
  width: "60px",
  height: "60px",
  padding: "10px",
  margin: "0px",
  '--ai-bg': 'rgb(0, 0, 0, 0.2)',
  '--ai-hover': 'rgb(255, 255, 255, 0.3)',
  pointerEvents: "auto",
}

function ToggleGraphs() {
  const displayGraphs = useSelector(state => state.graph.graphsVisible)
  const dispatch = useDispatch()

  return (
    <>
      <ActionIcon
        variant="filled"
        style={buttonStyle}
        className={displayGraphs ? 'buttonActive' : ''}
        onClick={() => dispatch(toggleGraphs())}
        styles={{
          root: {
            padding: '0',
            borderRadius: '100px',
          }
        }}
      >
        <GraphIcon />
      </ActionIcon>
    </>
  );
}

export default ToggleGraphs;