import { createSlice } from '@reduxjs/toolkit'

export const calendarSlice = createSlice({
  name: 'calendar',
  initialState: {
    calendarVisible: false
  },
  reducers: {
    toggleCalendar: state => {
        console.log('toggleCalendar visible (before):', state.calendarVisible)
        state.calendarVisible = !state.calendarVisible
        console.log('toggleCalendar visible (after):', state.calendarVisible)
    },
    hideCalendar: state => {
        console.log('hideCalendar visible (before):', state.calendarVisible)
        state.calendarVisible = false
        console.log('hideCalendar visible (after):', state.calendarVisible)
    }
  }
})

// Action creators are generated for each case reducer function
export const { toggleCalendar, hideCalendar } = calendarSlice.actions

export default calendarSlice.reducer